/**
 * Font LOAD
 */

const fontLoader = new FontLoader();
fontLoader.load(
  "./helvetica.json",
  // callback
  (font) => {
    const textGeometry = new TextGeometry(
      "Makura Creations\nconsole.log(true)",
      {
        font: font,
        size: 0.5,
        height: 0.1,
        curveSegments: 50,
        bevelEnabled: true,
        bevelThickness: 0.05,
        bevelSize: 0.02,
        bevelOffset: 0,
        bevelSegments: 4,
      }
    );

    // lets center Text
    // old way to center position
    // textGeometry.computeBoundingBox();
    // console.log(textGeometry.boundingBox);
    // textGeometry.translate(
    //   -(textGeometry.boundingBox.max.x - 0.02) * 0.5,
    //   -(textGeometry.boundingBox.max.y - 0.02) * 0.5,
    //   -(textGeometry.boundingBox.max.z - 0.01) * 0.5
    // );

    // latest way to center to position
    textGeometry.center();

    const textMaterial = new THREE.MeshNormalMaterial({
      // wireframe: true,
    });
    const text = new THREE.Mesh(textGeometry, textMaterial);
    scene.add(text);

    const donutGeometry = new TorusGeometry(0.2, 0.1, 20, 45);
    const donutMaterial = new THREE.MeshNormalMaterial({
      // wireframe: true,
    });
    const boxGeometry = new BoxBufferGeometry(0.1, 0.1, 0.1);
    const boxMaterial = new THREE.MeshNormalMaterial({
      // wireframe: true,
    });
    for (let i = 0; i < 200; i++) {
      const boxMesh = new THREE.Mesh(boxGeometry, boxMaterial);
      const donutMesh = new THREE.Mesh(donutGeometry, donutMaterial);
      donutMesh.position.x = (Math.random() - 0.5) * 10;
      donutMesh.position.y = (Math.random() - 0.5) * 10;
      donutMesh.position.z = (Math.random() - 0.5) * 10;
      boxMesh.position.x = (Math.random() - 0.5) * 15;
      boxMesh.position.y = (Math.random() - 0.5) * 15;
      boxMesh.position.z = (Math.random() - 0.5) * 15;

      donutMesh.rotation.x = Math.random() * Math.PI;
      donutMesh.rotation.y = Math.random() * Math.PI;

      const scale = Math.random();
      donutMesh.scale.x = scale;
      donutMesh.scale.y = scale;
      donutMesh.scale.z = scale;

      scene.add(donutMesh, boxMesh);
      const clock = new THREE.Clock();
      const animate = () => {
        const elapsedTime = clock.getElapsedTime();
        donutMesh.rotation.x = Math.sin(elapsedTime);

        window.requestAnimationFrame(animate);
      };
      animate();
    }
  }
);

