/**
 * camera
 */

const camera = new THREE.PerspectiveCamera(75, size.width / size.height);
camera.position.z = 6;
camera.position.x = -6.8;
camera.position.y = -1.6;
camera.lookAt(FontLoader)

gui.add(camera.position, "x", -10, 10);
gui.add(camera.position, "y", -10, 10);
gui.add(camera.position, "z", -10, 10);

// mouse movement
let mouse = {
  x: 0,
  y: 0,
  z: 0,
};
window.addEventListener("mousemove", (event) => {
  mouse.x = event.clientX / size.width - 0.5;
  mouse.y = event.clientY / size.height - 0.5;
  console.log(mouse.x, mouse.y);
});

window.addEventListener("resize", () => {
  // resize canvas

  size.width = window.innerWidth;
  size.height = window.innerHeight;
  //   update camera
  camera.aspect = size.width / size.height;
  camera.updateProjectionMatrix();
  renderer.setSize(size.width, size.height);
});

////////////////////////////////////////////////////////////////////////////
// END OF CAMERA
////////////////////////////////////////////////////////////////////////////

/**
 * Renderer
 */

const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
  alpha: true,
});
scene.add(camera);

////////////////////////////////////////////////////////////////////////////
// END OF RENDERER
////////////////////////////////////////////////////////////////////////////

// controls
// const controls = new OrbitControls(camera, canvas);
// controls.enableDamping = true;
renderer.setSize(size.width, size.height);
renderer.render(scene, camera);

////////////////////////////////////////////////////////////////////////////
// END OF CONTROLS
////////////////////////////////////////////////////////////////////////////

/**
 * Animation
 */

const clock = new THREE.Clock();

const animate = () => {
  const elapsedTime = clock.getElapsedTime();
  camera.position.x = (mouse.x * elapsedTime )/10 ;
  camera.position.y =( mouse.y * elapsedTime)/10 ;
 

  camera.rotation.x=Math.sin(elapsedTime) / 10;
  camera.rotation.y=Math.cos(elapsedTime) /10;
  // controls.update();
  // mesh.rotation.x= elapsedTime;
  renderer.render(scene, camera);
  window.requestAnimationFrame(animate);
};

animate();

////////////////////////////////////////////////////////////////////////////
